import React from 'react'

const fullAlpha = 'A-ZÀ-Üa-zà-ü'

export class RepresentanteLegal extends React.Component {

    constructor(){
        super()
        this.email = React.createRef()
        this.onChange = this.onChange.bind(this)
    }

    onChange(e){
        const {name, value} = e.target
        
        const target = {
            name : "representante_legal",
            value : {
                ...this.props.value,
                [name] : value
            }
        }

        this.props.onChange({target})
    }

    render(){
        const {nome, cpf, parentesco, telefone, email} = this.props.value

        const {hidden} = this.props

        const onChange = this.onChange

        return(
            <div hidden={hidden}>
                <h5 className="my-1 text-dark">Dados cadastrais do seu Representante Legal</h5>
    
                <div className="form-row">
                    <div className="form-group col-lg-10 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" placeholder="Informe o nome completo do seu representante legal, sem abreviações" value={nome} onChange={onChange} required minLength="7" maxLength="60" pattern={`^[${fullAlpha}][${fullAlpha} ]{5,58}[${fullAlpha}]$`}/>
                    </div>
    
                    <div className="form-group col-lg-2 col-md-3 col-sm-5 col-12">
                        <label htmlFor="cpf">CPF</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <input className="form-control" id="cpf" name="cpf" type="text" placeholder="somente números" value={cpf} onChange={onChange} required minLength="11" maxLength="11" pattern="^[0-9]{11}$"/>
                    </div>
    
                    <div className="form-group col-lg-3 col-md-9 col-sm-7 col-12">
                        <label htmlFor="parentesco">Parentesco</label>
                        <input className="form-control" id="parentesco" name="parentesco" type="text" placeholder="Pai, Mãe..." value={parentesco} onChange={onChange} required maxLength="20" pattern={`^.{2,20}$`}/>
                    </div>
    
                    <div className="form-group col-lg-3 col-md-4 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <input className="form-control" id="telefone" name="telefone" type="text" value={telefone} onChange={onChange} required placeholder="somente números" minLength="8" maxLength="11" pattern="^[0-9]{8,11}$"/>
                    </div>
    
                    <div className="form-group col-lg-6 col-md-8 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" defaultValue={email!==undefined ? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70"/>
                    </div>
                </div>
            </div>
        )
    }
}