import React from 'react'

const fullAlpha = 'A-ZÀ-Üa-zà-ü'

export class Supervisor extends React.Component {
    constructor(){
        super()
        this.email = React.createRef()
        this.onChange = this.onChange.bind(this)
    }

    onChange(e){
        const {name, value} = e.target

        const target = {
            name : "supervisor",
            value : {
                ...this.props.dados,
                [name] : value
            }
        }
        this.props.onChange({target})
    }

    render(){
        const {nome, cargo, formacao, telefone, email} = this.props.dados

        return (
            <div>
                <h5 className="my-1 text-dark">Quem será o supervisor do estágio no local escolhido?</h5>

                <div className="form-row">
                    <div className="form-group col-lg-7 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} onChange={this.onChange} required maxLength="60" pattern={`^[${fullAlpha}][${fullAlpha} ]{5,58}[${fullAlpha}]$`}/>
                    </div>

                    <div className="form-group col-lg-5 col-md-5 col-sm-12 col-12">
                        <label htmlFor="cargo">Cargo</label>
                        <input className="form-control" id="cargo" name="cargo" type="text" value={cargo} onChange={this.onChange} required maxLength="40" pattern={`^[${fullAlpha}][${fullAlpha} ]{4,38}[${fullAlpha}]$`}/>
                    </div>
                
                    <div className="form-group col-lg-4 col-md-7 col-sm-12 col-12">
                        <label htmlFor="formacao">Formação</label>
                        <input className="form-control" id="formacao" name="formacao" type="text" value={formacao} onChange={this.onChange} required maxLength="40" pattern={`^[${fullAlpha}][${fullAlpha} ]{4,38}[${fullAlpha}]$`}/>
                    </div>

                    <div className="form-group col-lg-3 col-md-4 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <input className="form-control" id="telefone" name="telefone" value={telefone} onChange={this.onChange} required placeholder="somente números" maxLength="11" pattern="[0-9]{8,11}"/>
                    </div>

                    <div className="form-group col-lg-5 col-md-8 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" type="email" id="email" name="email" defaultValue={email!==undefined? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70"/>
                    </div>
                </div>
            </div>
        )
    }
}