import React from 'react'

const fullAlpha = 'A-ZÀ-Üa-zà-ü'

export class CoordenadorDoCurso extends React.Component {
    constructor(){
        super()
        this.onChange = this.onChange.bind(this)
    }
    
    onChange(e) {
        const {name, value} = e.target

        const target = {
            name : "coordenador_curso",
            value : {
                ...this.props.dados,
                [name]: value
            }
        }

        this.props.onChange({target})
    }

    render(){
        const {nome, siape} = this.props.dados
        
        return(
            <div>
                <h5 className="my-1 text-dark">Quem é o Coordenador do Curso que você estuda?</h5>
            
                <div className="form-row">
                    <div className="form-group col-lg-10 col-md-8 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} onChange={this.onChange} required minLength="7" maxLength="60" pattern={`^[${fullAlpha}][${fullAlpha} ]{5,58}[${fullAlpha}]$`}/>
                    </div>

                    <div className="form-group col-lg-2 col-md-4 col-sm-12 col-12">
                        <label htmlFor="siape">SIAPE</label>
                        <input className="form-control" id="siape" name="siape" type="text" value={siape} onChange={this.onChange} required minLength="6" maxLength="8" pattern={`^[0-9]{6,8}$`}/>
                    </div>
                </div>
            </div>
        )
    }
}