import React from 'react'

export class PlanoDeAtividades extends React.Component {
    constructor(){
        super()
        this.onChange = this.onChange.bind(this)
    }
    
    onChange(e) {
        const {name, value} = e.target

        //evita quebra de linha
        const valorFiltrado = value.replace(/(?:\r\n|\r|\n)/g, '');

        const target = {
            name : "plano_atividades",
            value : {
                ...this.props.dados,
                [name]: valorFiltrado
            }
        }

        this.props.onChange({target})
    }

    render(){
        const {objetivos, area, atividades} = this.props.dados

        return(
            <div>
                <h5 className="my-1 text-dark">Qual o Plano de Atividades a ser desenvolvido no estágio?</h5>

                <div className="form-row">
                    <div className="form-group col-lg-6 col-md-12 col-sm-12 col-12">
                        <label htmlFor="area">Área</label>
                        <label className="badge badge-pill badge-info">quantidade de caracteres: no mínimo 10 e no máximo 80</label>
                        <textarea className="form-control" id="area" name="area" type="text" value={area} onChange={this.onChange} required minLength="10" maxLength="80"></textarea>
                    </div>

                    <div className="form-group col-lg-6 col-md-12 col-sm-12 col-12">
                        <label htmlFor="objetivos">Objetivos</label>
                        <label className="badge badge-pill badge-info">quantidade de caracteres: no mínimo 10 e no máximo 120</label>
                        <textarea className="form-control" id="objetivos" name="objetivos" value={objetivos} onChange={this.onChange} required minLength="10" maxLength="120"></textarea>
                    </div>

                    <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                        <label htmlFor="atividades">Atividades</label>
                        <label className="badge badge-pill badge-info">quantidade de caracteres: no mínimo 10 e no máximo 500</label>
                        <textarea className="form-control" id="atividades" name="atividades" type="text" value={atividades} onChange={this.onChange} required minLength="10" maxLength="500"></textarea>
                    </div>
                </div>
            </div>
        )
    }
}